export default function todoApp(state = {list:[]}, action) {
  switch (action.type) {
    case 'ADDTODO':
      
    	const newLst = [
	        ...state.list,
	        {
	          id: 1 + Math.random(),
	          value: action.payload
	        }
	      ];
        return { ...state , list: newLst};
    case 'DELETETODO':
        return { ...state , list: state.list.filter((item) => item.id !== action.payload)};
    case 'RESET':
        return { ...state , list: [] };
    case 'LOADTODO':
        return { ...state , list: action.payload };
    default:
      return state
  }
}